let initialstate = 10;
const increDecre = (state = initialstate, action) => {
    switch(action.type){
        case "INCREMENT":
            return state + action.payload;
        case "DECREMENT":
            if(state===0){
                alert("you have achieved minimum number")
                return state;
            } else {
                return state - action.payload;
            }
        default:
            return state;
        }
}

export default increDecre
