import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { useSelector, useDispatch } from 'react-redux'
import {increment, decrement} from './actions/index'
function App() {
  
  const counter = useSelector(state => state.increDecre)
  const dispatch = useDispatch()

  return (
    <>
      <div className="container text-center" style={{ width: "150px" }}>
        <h2>App</h2>
        <p>Counter : { counter }</p>
        <div className="d-flex justify-content-between">
          <button className="btn btn-outline-success" onClick={()=>{dispatch(increment(5))}} >+</button>
          <button className="btn btn-outline-warning" onClick={()=>{dispatch(decrement(1))}} >-</button>
        </div>
      </div>
    </>
  )
}

export default App
